const Turn = props => {
  let fadeClass = useRef(null);
  
  useEffect(() => {
    if (fadeClass) {
      $(fadeClass).turn(Object.assign({}, props.options));
    }
    // 	//   document.addEventListener("keydown", handleKeyDown, false);
  }, [props.options]);
  
  return (
    <div
      className={props.className}
      style={Object.assign({}, props.style)}
      ref={el => {
        fadeClass = el;
      }}
    >
      {props.children}
    </div>
  );
};



