const options = {
  height: '100%',
  width: '100%',
  display: "double",
  acceleration: true,
  elevation: 50,
  gradients: true,
  autoCenter: true,
  duration: 1000,
};

const Home = () => {
  const {width} = useWindowSize()
  
  useEffect(() => {
    document.documentElement.style.overflow = 'hidden';
    
    return () => {
      document.documentElement.style.overflow = 'auto'
    }
  }, []);
  
  return (
    <>
      <div className="home_container">
        <div className="home_flipbook_container">
          <Turn
            options={options}
            className="flipbook"
            style={{transition: '0.6s'}}
          >
            {(width > 480 ? pages : mobpages).map((page, index) => (
              <div key={index} className="page">
                <img src={page} alt="" />
              </div>
            ))}
          </Turn>
        </div>
      </div>
    </>
  );
};

export default Home;
